import VueRouter from 'vue-router'
import Vue from 'vue'
import login from '../components/testLogin.vue'
import App from '../App.vue'

Vue.use(VueRouter)

const router =  new VueRouter({
    mode: 'history',
    routes : [
        {path:'/', component: App },
        {path:'/login', component: login}
    ]
})


export default router